import React from 'react';
import PropTypes from 'prop-types'
import Backdrop from '@material-ui/core/Backdrop';
import CircularProgress from '@material-ui/core/CircularProgress';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: 10
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
    },
}));

let handler = {}

export const backDropApi = {
    open: () => {
        handler.setOpen(true)
    },
    clsoe: () => {
        handler.setOpen(false)
    }
}

const BackdorpControl = ({ defaultOpen, message }) => {

    message = message || ""
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    React.useEffect(() => {
        handler.setOpen = setOpen
    }, []

    )

    return (
        <div>
            <Backdrop className={classes.backdrop} open={open || defaultOpen} >
                <div className={classes.root}>
                    <div><CircularProgress color="inherit" /></div>
                    <div><Typography>{message} </Typography></div>
                </div>
            </Backdrop>
        </div>
    );
}

BackdorpControl.propTypes = {
    defaultOpen: PropTypes.bool,
    message: PropTypes.string
}

export default BackdorpControl;