import React from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Typography from '@material-ui/core/Typography';
import { Link, Redirect, withRouter } from 'react-router-dom';

const HeaderButton = ({ menuName, onClick, isLoggedIn }) => {
  
  const click=()=>
  {
    onClick(isLoggedIn);
  }
  return (
    <>
    <Button aria-controls="simple-menu" aria-haspopup="true" onClick={()=>click()}>
        <Typography variant="h6" gutterBottom style={{ color: 'white' }}>          
            <Link style={{ textDecoration: 'none', boxShadow: "none", color: 'white' }} to={"/"} href="#">
              {menuName}
            </Link>
        </Typography>
      </Button>     
    </>

  );
}
export default withRouter(HeaderButton);