import React, { useEffect, useState } from 'react'
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { Avatar, Button, Tooltip } from '@material-ui/core';
import CustomMenu from './Controls/CustomMenu';
import HeaderButton from './Controls/HeaderButton';
import { makeStyles } from '@material-ui/core/styles';
import { Redirect } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        '& > *': {
            margin: theme.spacing(1),
        },
    },
    small: {
        width: theme.spacing(3),
        height: theme.spacing(3),
    },
    med: {
        width: theme.spacing(4),
        height: theme.spacing(4),
    },
    large: {
        width: theme.spacing(7),
        height: theme.spacing(7),
    },
}));

const Header = () => {

    const [isLoggedIn, setLoggedIn] = useState(false)
    const classes = useStyles();

    let newSubMenus = [
        {
            Name: "User",
            Path: "/Users/New"
        },
        {
            Name: "Student",
            Path: "/Students/New"
        },
        {
            Name: "Teacher",
            Path: "/Teachers/New"
        }
    ]

    let listSubMenus = [
        {
            Name: "Users",
            Path: "/Users"
        },
        {
            Name: "Students",
            Path: "/Students"
        },
        {
            Name: "Teachers",
            Path: "/Teachers"
        }
    ]

    let authSubMenus = [
        {
            Name: "SignIn",
            Path: "/SignIn"
        },
        {
            Name: "SignUp",
            Path: "/SignUp"
        }
    ]

    const login = (islogin) => {
        setLoggedIn(islogin)
        Redirect("/Home")
    }

    return (
        <>
            <Grid container alignItems="center">
                <Grid>
                    <Typography variant="h4" gutterBottom style={{ color: 'white', paddingTop: "5px" }}>
                        Test Application
                    </Typography>
                </Grid>
                <Grid style={{ marginLeft: 'auto' }}>
                    {
                        isLoggedIn == true ?
                            <>
                                <CustomMenu menuName="HOME" />
                                <CustomMenu menuName="NEW" subMenus={newSubMenus} />
                                <CustomMenu menuName="LIST" subMenus={listSubMenus} />
                                <CustomMenu menuName="Auth" subMenus={authSubMenus} />
                                {
                                    //                               <HeaderButton menuName="LOGOUT" onClick={login} isLoggedIn={false} />                           
                                }
                                <Button onClick={login} isLoggedIn={false}>
                                    <Tooltip title="Logoff" >
                                        <Avatar className={classes.med}></Avatar>
                                    </Tooltip>
                                </Button>

                            </>
                            :
                            <HeaderButton menuName="LOGIN" onClick={login} isLoggedIn={true} />

                    }
                    {
                        // <NavigationBar></NavigationBar>
                    }

                </Grid>
            </Grid>
        </>
    )
}

export default Header;