import React from 'react';
import { AgGridColumn, AgGridReact } from 'ag-grid-react';
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import Typography from '@material-ui/core/Typography';
import MockData from '../../MockData/Teachers.json'
function TeachersList() {

    let teachersData = MockData
    console.log(MockData);

    return (
        <div style={{ padding: '10px' }}>
            <Typography variant="h5" component="h2">
                Teachers List
            </Typography>
            <div className="ag-theme-alpine" style={{ height: '450px', width: 'auto' }}>
                <AgGridReact rowData={teachersData} pagination={true} paginationPageSize="10"  >
                    <AgGridColumn field="id" ></AgGridColumn>
                    <AgGridColumn field="first_name" headerName="First Name"></AgGridColumn>
                    <AgGridColumn field="last_name" headerName="Last Name"></AgGridColumn>
                    <AgGridColumn field="gender"></AgGridColumn>
                    <AgGridColumn field="email"></AgGridColumn>
                    <AgGridColumn field="username"></AgGridColumn>
                    <AgGridColumn field="color"></AgGridColumn>
                </AgGridReact>
            </div>
        </div>
    );
}

export default TeachersList;