import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import PropTypes from 'prop-types';

let handler = {}

export const dialogApi =
{
  show: (title, content, action) => {
    handler.setOpen(true)
    handler.setTitle(title)
    handler.setContent([content])
    handler.setAction(action)
  }
  ,
  showContentList: (title, content, action) => {
    handler.setOpen(true)
    handler.setTitle(title)
    handler.setContent(content)
    handler.setAction(action)
  },

}

const DialogBox = ({ handleAction }) => {
  const [open, setOpen] = React.useState(false);
  const [title, setTitle] = React.useState('');
  const [content, setContent] = React.useState('');
  const [action, setAction] = React.useState('');

  React.useEffect(() => {
      handler.setOpen = setOpen
      handler.setTitle = setTitle
      handler.setContent = setContent
      handler.setAction = setAction
  }, []
  )

  const handleClose = () => {
    setOpen(false);
  };

  const handleOk = async() => {
    setOpen(false)
    await handleAction(action)
  };

  
  return (
    <div>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">{title}</DialogTitle>
        <DialogContent>
          {
            content ?
              content.map((con, index) =>
                <DialogContentText key={index} id="dialog-description-content-text">
                  {con}
                </DialogContentText>
              ) : null
          }
        </DialogContent>
        <DialogActions>
          <Button onClick={handleOk} color="primary">
            Ok
          </Button>
          <Button onClick={handleClose} color="primary" autoFocus>
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

DialogBox.propTypes = {
  handleAction: PropTypes.func
}

export default DialogBox