import React, { useEffect, useState } from 'react';
import { AgGridColumn, AgGridReact } from 'ag-grid-react';
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import Typography from '@material-ui/core/Typography';
import ButtonRederer from '../AGGridControls/AgGridButtonRenderer'
import StatusRederer from '../AGGridControls/AgGridStatusRenderer'
import DialogBox, { dialogApi } from '../Controls/DialogBox';
import Backdrop from '../Controls/BackdropControl';
function UsersList() {

    const [data, setData] = useState([]);
    const [selId, setSelId] = useState('')
    const getData = () => {
        fetch('https://gorest.co.in/public/v1/users'
            , {
                headers: {
                    'Content-Type': 'application/json',
                    'Accept': 'application/json'
                }
            }            
        )
            .then(function (response) {
                console.log(response)
                return response.json();
            })
            .then(function (myJson) {
                console.log(myJson);
                
                setTimeout(() => {  setData(myJson.data);}, 1000);
               
            });
    }
    useEffect(() => {
        getData()
    }, [])


    const userAction = async (id, action) => {
        console.log(id, action);
        if (action == 'Delete') {
            setSelId(id)
            dialogApi.show("Delete Confirmation", "Deleting the user from the records", "Delete")

        } else {
            alert('action completed' + 'Student id: ' + id + ' Action: ' + id);
        }

    }

    const handleDeleteConfirmation = async () => {
        let usersData = Object.values(data)
        console.log(selId)
        setData(usersData.filter(item => item.id !== selId))
    
        
    }

    return (
        <div style={{ padding: '10px' }}>
            <Typography variant="h5" component="h2">
                Users List
            </Typography>
            <div className="ag-theme-alpine" style={{ height: '450px', width: 'auto' }}>
                {data.length ?
                    <AgGridReact
                        frameworkComponents={{
                            buttonRederer: ButtonRederer,
                            statusRenderer: StatusRederer
                        }
                        }
                        rowData={data}
                        skipHeaderOnAutoSize="true"
                    >
                        <AgGridColumn width={100} field="id" headerName="" cellRenderer="buttonRederer" cellRendererParams={{ caption: "CRUD", handleClick: userAction }} ></AgGridColumn>
                        <AgGridColumn field="status" cellRenderer="statusRenderer" width={100}
                            headerName="Active" ></AgGridColumn>
                        <AgGridColumn field="id" ></AgGridColumn>
                        <AgGridColumn field="name" headerName="Name"></AgGridColumn>
                        <AgGridColumn field="email" headerName="Email"></AgGridColumn>
                        <AgGridColumn field="gender"></AgGridColumn>
                        <AgGridColumn field="status"></AgGridColumn>
                    </AgGridReact>
                    : <Backdrop defaultOpen={true}  message="Fetching data" />
                }
                <DialogBox handleAction={handleDeleteConfirmation} />
            </div>
        </div>
    );
}

export default UsersList;