import { Button } from '@material-ui/core'
import React from 'react'
import CancelIcon from '@material-ui/icons/Cancel';
import BorderColorIcon from '@material-ui/icons/BorderColor';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import { green, orange, red } from '@material-ui/core/colors';
function AgGridButtonRenderer(props) {
   // console.log(props.value)
    const handleAddClick = async () => {
        await props.handleClick(props.value, 'Add');
        //alert('Add')
    }
    const handleDeleteClick = async () => {
        //alert('Delete')
        await props.handleClick(props.value, 'Delete');
    }
    const handleEditClick = async () => {
        //alert('Edit')
        await props.handleClick(props.value, 'Edit');
    }
    return (
        <>
            
            <CancelIcon style={{ color: red[400] }} onClick={handleDeleteClick} ></CancelIcon>
            {
                //<BorderColorIcon style={{ color: orange[400] }} onClick={handleEditClick} ></BorderColorIcon>
                //           <AddCircleIcon style={{ color: green[400] }} onClick={handleAddClick} ></AddCircleIcon>
            }

        </>
    );
}
export default AgGridButtonRenderer;