import React, { useState } from 'react';
import { AgGridColumn, AgGridReact } from 'ag-grid-react';
import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';
import Typography from '@material-ui/core/Typography';
import MockData from '../../MockData/Students.json'
import ButtonRederer from '../AGGridControls/AgGridButtonRenderer'
import StatusRederer from '../AGGridControls/AgGridStatusRenderer'
import DialogBox, { dialogApi } from '../Controls/DialogBox';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

function StudentsList() {

  //let studentsdata = MockData
  const [studentsdata, setStudentData] = useState(MockData)
  const [selStudent, setSelStudent] = useState('')
  console.log(MockData);



  const studentAction = async (studentid, action) => {
    console.log(studentid, action);
    if (action == 'Delete') {
      setSelStudent(studentid)
      dialogApi.show("Delete Confirmation", "Deleting the student from the records", "Delete")

    } else {
      alert('action completed' + 'Student id: ' + studentid + ' Action: ' + action);
    }

  }

  const handleDeleteConfirmation = async () => {
    //alert(selStudent)
    let students = Object.values(studentsdata)
    console.log('before filter', students)
    setStudentData(students.filter(item => item.id !== selStudent))

    console.log('after filter', studentsdata)

  }

  function onMygridReady(params) {
    //params.api.sizeColumnsToFit();
  }

  return (
    <div style={{ padding: '10px' }}>
      <Typography variant="h5" component="h2">
        Student List
      </Typography>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography >Grid Features</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            You can sort the columns by clicking on the column header. You can filter data from the column header.
          </Typography>
        </AccordionDetails>
      </Accordion>
    
      <div className="ag-theme-alpine" style={{ height: 350, width: 'auto' }}>
        <AgGridReact pagination={true} paginationPageSize="10"
          frameworkComponents={{
            buttonRederer: ButtonRederer,
            statusRenderer: StatusRederer
          }
          }
          rowData={studentsdata}
          skipHeaderOnAutoSize="true"
          resizable="true"
          onGridReady={onMygridReady}
        >
          <AgGridColumn width={110} field="id" cellRenderer="buttonRederer"
            cellRendererParams={{ caption: "CRUD", handleClick: studentAction }}
            headerName=""></AgGridColumn>
          <AgGridColumn field="activeStatus" cellRenderer="statusRenderer" width={90}
            headerName="Active" ></AgGridColumn>
          <AgGridColumn field="id" width={60} ></AgGridColumn>
          <AgGridColumn field="firstname" headerName="First Name" sortable={true} filter={true} editable={true}></AgGridColumn>
          <AgGridColumn field="lastname" headerName="Last Name" sortable={true} filter={true} editable={true}></AgGridColumn>
          <AgGridColumn field="gender" sortable={true} filter={true} editable={true}></AgGridColumn>
          <AgGridColumn field="email" sortable={true} filter={true} editable={true}></AgGridColumn>
          <AgGridColumn field="username" sortable={true} filter={true} editable={true}></AgGridColumn>
          <AgGridColumn field="color" sortable={true} filter={true} editable={true}></AgGridColumn>
        </AgGridReact>
        <DialogBox handleAction={handleDeleteConfirmation} />
      </div>
    </div>
  );
}
export default StudentsList;