import React from 'react'
import Typography from '@material-ui/core/Typography';
import {Link} from 'react-router-dom';

function NotFound()
{
    return(
        <>
        <br/><br/><br/><br/><br/>
        <Typography variant="h5" style={{textAlign:'center'}} > We dont found this page in application. This is invalid operation.</Typography>
        <Typography variant="h8" style={{textAlign:'center'}} > Link</Typography>
       <Link to="/" style={{textAlign:'center', alignItems:'right'}}>Go Home</Link>
        
        </>
    )
}

export default NotFound;