import React,{useState} from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import { Container } from '@material-ui/core';
import Box from '@material-ui/core/Box';
import SwitchControl  from '../Controls/SwitchControl';

const useStyles = makeStyles((theme) => ({
  seeMore: {
    marginTop: theme.spacing(3),
    marginLeft: theme.spacing(3),
    marginRight: theme.spacing(3),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2),
    background: "#b3d9ff"
  },
  header: {
    color: "red"
  },
}));

function UserEntry() {
const [userData,setUserData] = useState({name:"Active", label:"Is Active", value:false})
const handleChange =(itemName, itemValue)=>{
  setUserData({name:"Active", label:"Is Active", value:itemValue})
}

const updateField =(itemName, itemValue)=>{

}
  return (
    <Container maxWidth={false}>
      <Paper style={{ padding: '2px' }} >
        <Box display="flex" p={1} m={1} bgcolor="background.paper" >
          <Typography variant="h5" component="h2">
            User Details:
          </Typography>
        </Box>
        <form autoComplete="off">
          <Grid container spacing={1} id="121" >
            <Grid item xs={12} sm={6} >
              <TextField
                required
                id="firstName"
                name="firstName"
                label="First Name"
                fullWidth
                // autoComplete="given-name"
                variant="outlined"
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                required
                id="lastName"
                name="lastName"
                label="Last name"
                fullWidth
                // autoComplete="family-name"
                variant="outlined"
              />
            </Grid>
            <Grid item xs={12} sm={6} >
              <TextField
                required
                id="bithDate"
                name="birthDate"
                label="Birth Date"
                fullWidth
                // autoComplete="given-name"
                variant="outlined"
              />
            </Grid>
            <Grid item xs={12} sm={6} >
              <TextField
                required
                id="anniversaryDate"
                name="anniversaryDate"
                label="Anniversary Date"
                fullWidth
                // autoComplete="given-name"
                variant="outlined"              />
            </Grid>
            <Grid item xs={12} sm={6} >
              <TextField
                required
                id="emailAddress"
                name="emailAddress"
                label="Email Address"
                fullWidth
                // autoComplete="given-name"
                variant="outlined"
              />
            </Grid>
            
            <Grid item xs={12}>
              <TextField
                required
                id="address1"
                name="address1"
                label="Address line 1"
                fullWidth
                //  autoComplete="shipping address-line1"
                variant="outlined"
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                id="address2"
                name="address2"
                label="Address line 2"
                fullWidth
                //autoComplete="shipping address-line2"
                variant="outlined"
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                required
                id="city"
                name="city"
                label="City"
                fullWidth
                //autoComplete="shipping address-level2"
                variant="outlined"
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField id="state" name="state" label="State/Province/Region" fullWidth variant="outlined" />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                required
                id="zip"
                name="zip"
                label="Zip / Postal code"
                fullWidth
                //autoComplete="shipping postal-code"
                variant="outlined"
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                required
                id="country"
                name="country"
                label="Country"
                fullWidth
                // autoComplete="shipping country"
                variant="outlined"
              />
            </Grid>
            <Grid item xs={12}>
              {
               // <FormControlLabel
                //control={<Checkbox color="secondary" name="isActive" value="yes" />}
                //label="Acitve"   />
}
              <SwitchControl {...userData} handleChange={handleChange} updateField={updateField}/>
            </Grid>
          </Grid>
        </form>
      </Paper>
    </Container>
  );
}

export default UserEntry;