import React from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Typography from '@material-ui/core/Typography';
import { Link, Redirect, withRouter } from 'react-router-dom';

const CustomMenu = ({ menuName, subMenus }) => {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
    <Button aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
        <Typography variant="h6" gutterBottom style={{ color: 'white' }}>
          {subMenus ?
            menuName :
            <Link style={{ textDecoration: 'none', boxShadow: "none", color: 'white' }} to={"/"} href="#">
              {menuName}
            </Link>}
        </Typography>
      </Button>
      {subMenus ?
        <Menu
          id="simple-menu"
          anchorEl={anchorEl}
          keepMounted
          open={Boolean(anchorEl)}
          onClose={handleClose}
        >
          {

            subMenus.map((item, index) => (
              <MenuItem key={'Key' + index + menuName} onClick={handleClose} style={{ paddingLeft: 13, textDecoration: 'none' }}>
                <Link tyle={{ boxShadow: "none" }} to={item.Path} href="#"><span>{item.Name}</span></Link>
              </MenuItem>
            ))

          }
        </Menu>
        : ""
      }
    </>

  );
}
export default withRouter(CustomMenu);