import './App.css';
import React from 'react';
import Student from "./Components/Student"
import Teacher from "./Components/Teacher"
import HomePage from "./Components/HomePage"
import { BrowserRouter, Route, Switch, Link, NavLink } from 'react-router-dom';
import Users from './Components/Users';
import NotFound from './Components/NotFound';
import StudentEntry from './Components/EntryScreens/StundentEntry';
import TeacherEntry from './Components/EntryScreens/TeacherEntry';
import UserEntry from './Components/EntryScreens/UserEntry';
import SignIn from './Components/Authentication/SignIn';
import SignUp from './Components/Authentication/SignUp';
import Header from "./Components/Header"

function App() {
  //const nodeRef = React.useRef(null);
  return (
    <BrowserRouter >
      < >
        <header className="App-header">
          <Header></Header>
        </header>
        <div>
          <Switch>
            <Route exact path="/"></Route>
            <Route path="/Students/New" component={StudentEntry} />
            <Route path="/Teachers/New" component={TeacherEntry} />
            <Route path="/Users/New" component={UserEntry} />

            <Route path="/Students" component={Student} />
            <Route path="/Teachers" component={Teacher} />
            <Route path="/Users" component={Users} />
            <Route path="/HomePage" component={HomePage} />
            <Route path="/SignIn" component={SignIn} />
            <Route path="/SignUp" component={SignUp} />
            <Route component={NotFound} />
          </Switch>
        </div>
      </>
    </BrowserRouter>
  );
}
export default App;
